* {
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

main {
  width: 80%;
  height: -moz-fit-content;
  height: fit-content;
  margin-inline: auto;
  margin-top: 2%;
}

button:hover {
  cursor: pointer;
}

nav {
  background-color: #ffcf00;
  justify-content: space-around;
  align-items: center;
  padding: .5em;
  display: flex;
}

nav img {
  width: 9%;
}

a {
  color: #000;
  margin: .1em;
  padding: 5px;
  font-size: 1em;
  text-decoration: none;
}

span {
  margin-left: .5em;
  padding: .1em;
}

nav div, nav a {
  align-items: center;
  display: flex;
}

div > * {
  margin: 0 1em;
}

#logo:hover {
  cursor: pointer;
}

.item-container {
  border-top: 2px solid #d3d3d3;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: .5%;
  display: flex;
}

.item-card {
  width: 23%;
  min-width: 300px;
  max-width: 400px;
  border: 1px solid #f006;
  border-radius: 10px;
  flex-direction: column;
  flex-grow: 1;
  justify-content: end;
  margin: .5%;
  padding: .4%;
  display: flex;
}

.item-container > div > * {
  margin: 2px;
}

.item-container a {
  color: #000;
}

.add-to-cart-btn {
  width: -moz-fit-content;
  width: fit-content;
  background-color: #ffcf00;
  border-radius: 10px;
  padding: 10px;
}

select {
  width: -moz-fit-content;
  width: fit-content;
  border: 1px solid #ccc;
  border-radius: 1em;
  padding: 1em;
}

.filter-button {
  border: 1px solid #ccc;
  border-radius: 1em;
  margin-left: 1em;
  padding: 1em;
}

.hover:hover {
  cursor: pointer;
}

.out-of-stock-card {
  z-index: 10;
  color: red;
  width: -moz-fit-content;
  width: fit-content;
  border: 3px solid red;
  padding: 20px;
  font-size: 2em;
  font-weight: bolder;
  position: relative;
  top: 3em;
  left: 7%;
  transform: rotate(-45deg);
}

.img-out-of-stock {
  opacity: .5;
}

@media only screen and (width <= 1000px) {
  main {
    width: 100%;
  }
}

@media only screen and (width <= 750px) {
  .item-card {
    max-width: none;
    width: 90%;
    flex-grow: 0;
  }

  .out-of-stock-card {
    font-size: 3em;
  }
}

#shopping-cart {
  width: 80%;
  height: -moz-fit-content;
  height: fit-content;
  border: 1px solid #000;
  border-radius: 1em;
  margin: 1% auto;
  display: flex;
}

#cart {
  width: 75%;
  margin: 0;
}

#order {
  width: 25%;
  text-align: center;
  height: -moz-fit-content;
  height: fit-content;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

#order > * {
  margin: 5% 0;
}

.cart-item {
  width: 100%;
  min-height: 100px;
  border: 1px solid #d3d3d3;
  border-radius: 1em;
  justify-content: space-between;
  align-items: center;
  margin: .5%;
  display: flex;
}

.cart-item div {
  align-items: center;
  display: flex;
}

.cart-item-img {
  max-width: 200px;
}

.order-btn {
  background-color: #ffcf00;
  border-radius: 1em;
  padding: 1em;
}

.product-title {
  max-width: 25%;
}

.product-amount {
  margin-right: 0;
}

@media only screen and (width <= 750px) {
  #shopping-cart {
    flex-direction: column;
    align-items: center;
  }

  .cart-item, .cart-item div {
    flex-direction: column;
  }

  .product-title {
    max-width: 50%;
  }
}

